$h-text:white;
$highlight:rgb(61, 216, 61);
$background:rgb(3, 3, 3); 
$fade-text:rgb(168, 168, 168);
$card-background:rgb(29, 29, 29);

$h-font:monospace;

*{
    margin: 0;
    padding: 0;
}
nav{
    background-color: $background;
    padding:20px;
   
   

    h1{
        
        color:$h-text;
        font-size: 2rem;
        // font-family: $h-font
    }
    h3{
        color: $highlight;
        font-size: 1.5rem;
    }
}

.home-body{
    background-color: $background;
    padding: 10px;
    

.section-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    
    
    h2{
     color:$highlight;  
     padding: 10px; 
    }

    
    hr{
        border-style: none none solid none;
        border-width: 1px;
        border-color: $highlight;
        width: 75%;
        margin: 10px;
    }
    .section{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;

        .video-card{
            display:flex;
            flex-direction: column;
            background-color:$card-background ;
            width: 40%;
            overflow: hidden;
            margin: 10px;
            border-radius: 10px;
            
            img{
                width: 100%;
                

            }
            
            .video-details{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                color: $fade-text;
                padding: 10px;
                h4{
                    color: $h-text;
                    
                    font-family: $h-font;
                }

                .card-footer{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    p{
                        font-size:small;
                        
                    }
                }
                
                
            }
        } 
    }
}
    
    
}

